"use client";

import { useState } from "react";

export default function AdminDoctors() {
  const [searchTerm, setSearchTerm] = useState("");

  const doctors = [
    { name: "Dr. Emma Johnson", specialty: "Cardiology", appointments: 24, status: "Active" },
    { name: "Dr. Michael Smith", specialty: "Dermatology", appointments: 18, status: "Inactive" },
    { name: "Dr. Olivia Brown", specialty: "Neurology", appointments: 30, status: "Active" },
  ];

  const filteredDoctors = doctors.filter((doctor) =>
    doctor.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div>
      <h2 className="text-3xl font-bold mb-6 text-gray-800 flex items-center gap-2">
        Doctors 
      </h2>

      <input
        type="text"
        placeholder="ðŸ”Search by doctor name..."
        className="w-full mb-4 p-3 border border-gray-300 rounded-lg"
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
      />

      <table className="w-full bg-white rounded-xl shadow overflow-hidden">
        <thead className="bg-gray-100 text-left">
          <tr>
            <th className="p-4">Name</th>
            <th className="p-4">Specialty</th>
            <th className="p-4">Appointments Handled</th>
            <th className="p-4">Status</th>
            <th className="p-4">Actions</th>
          </tr>
        </thead>
        <tbody>
          {filteredDoctors.map((doctor, index) => (
            <tr key={index} className="border-b">
              <td className="p-4">{doctor.name}</td>
              <td className="p-4">{doctor.specialty}</td>
              <td className="p-4">{doctor.appointments}</td>
              <td className="p-4">{doctor.status}</td>
              <td className="p-4">
                <button
                  onClick={() => {
                    const updated = [...doctors];
                    updated[index].status =
                      updated[index].status === "Active" ? "Inactive" : "Active";
                    setSearchTerm(""); // clear search to reflect change immediately
                  }}
                  className="px-3 py-1 border rounded-lg"
                >
                  {doctor.status === "Active" ? "Deactivate" : "Activate"}
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

